<template>
<div>
    <basic-header :items="['店铺管理','配送管理']"></basic-header>
    <el-card>
        <el-tabs v-model="activeName">
            <el-tab-pane label="模板查看" name="first">
                <el-table :data="tableData" border style="width: 100%">
                    <el-table-column prop="id" label="ID"></el-table-column>
                    <el-table-column prop="name" label="模板名称"></el-table-column>
                    <el-table-column prop="type" label="计费方式">
                        <template #default="o">
                            <span v-if="o.row.type===1">重量</span>
                            <span v-else>数量</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="create_time" label="创建时间"></el-table-column>
                    <el-table-column prop="update_time" label="修改时间"></el-table-column>
                    <el-table-column prop="operating" label="操作">
                        <template #default="o">
                            <el-button type="text" @click="edit(o.row)">修改</el-button>
                            <el-button type="text" @click="del(o.row.id)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <!--分页-->
                <el-pagination
                        background
                        layout="prev, pager, next"
                        :current-page.sync="page"
                        :page-size.sync="pageSize"
                        :total="total"
                >
                </el-pagination>
            </el-tab-pane>

            <el-tab-pane label="模板添加" name="second">
                <el-col :span="12">
                    <el-form label-width="80px">
                        <el-form-item label="模板名称">
                            <el-input></el-input>
                        </el-form-item>
                        <el-form-item label="模板类型">
                            <el-radio-group v-model="editForm.type">
                                <el-radio :label="1">按重量计费</el-radio>
                                <el-radio :label="2">按数量计费</el-radio>
                            </el-radio-group>
                        </el-form-item>
<!--                        <el-form-item>-->
<!--                            <el-button type="primary" @click="submit">添加</el-button>-->
<!--                        </el-form-item>-->
                    </el-form>
                </el-col>
            </el-tab-pane>
        </el-tabs>
        <el-dialog title="提示" :visible.sync="dialogFormVisible">
            <el-form :model="editForm" label-width="80px">
                <el-tab>
                    <el-tab-pane label="模板规则">

                    </el-tab-pane>
                    <el-tab-pane label="规则添加" name="add">
                        <el-form>
                            <el-form-item label="地区选择"></el-form-item>
                        </el-form>
                    </el-tab-pane>
                </el-tab>
                <el-form-item label="模板名称" prop="name">
                    <el-input></el-input>
                </el-form-item>
                <el-form-item label="配送模板">
                    <el-radio-group v-model="editForm.type">
                        <el-radio :label="1">按重量计费</el-radio>
                        <el-radio :label="2">按数量计费</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="配送规则">
                    <el-table :border="true" :data="ruleData">
                        <el-table-column label="地区" prop="area"></el-table-column>
                        <el-table-column label="价格" prop="price"></el-table-column>
                        <el-table-column label="操作">
                            <template>
                                <el-button type="text">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
            </div>
        </el-dialog>
    </el-card>
</div>
</template>

<script>
    import Header from "../../components/Header";
    export default {

        data() {
            return {
                activeName:"first",
                api:"/api/delivery/delivery",
                page:1,
                pageSize:5,
                total:0,
                tableData:[],
                ruleData:[],
                editForm:{},
                dialogFormVisible: false,
                submitForm:{
                    name:"",
                    type:""
                },
                rules:{
                    name:{},
                    type:{}
                },
                rules2:{

                },
                ruleForm:{}

            }
        },
        methods: {
            fetchData(){
                this.axios.get(this.api,{
                    params:{
                        page:this.page,
                        pageSize:this.pageSize,
                    }
                }).then(res=>{
                    this.tableData=res.data.data;
                    this.total=res.data.total;
                })
            },
            change(){
                this.fetchData()
            },
            //配送编辑
            edit(obj){
                this.dialogFormVisible=true;
                this.editForm=obj;
                this.axios.get("/api/rules/rules",{params:{did:obj.id}}).then(res=>{
                    if (res.data.code===200){
                        this.editForm=res.data.data;
                    }
                })
            },

            //封装fetchRules(id){}
            //删除
            del(id){
                this.$confirm('此操作将永久删除分类, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete(this.api, {
                        params: {id}
                    }).then(res => {
                        if (res.data.code === 200) {
                            this.$message.success("删除成功");
                            this.fetchData();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            handleSelect(data){
                this.ruleForm.area=data.area.value;
            },
            //添加规则方法
            addRules(){
                this.$refs.ruleForm.validate(valid=>{
                    if (valid){
                        this.ruleForm.delivery_id=this.editForm.id;
                        this.axios.post("api/rules/rules".$this.ruleForm).then(res=>{
                            if (res.data.code===200){
                                this.$message.success("添加成功");
                                this.$refs.ruleForm.resetFields();
                                this.activeName2="edit";

                            }
                        })
                    }
                })
            }



            // handleExpandChange(){
            //
            // },
        },
        components:{
            "basic-header":Header
        },
        created() {
            this.fetchData()
        },


        mounted() {

        }
    }
</script>

<style scoped>

</style>